import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_CLICK_TOOLBAR = Symbol('TABLE_CLICK_TOOLBAR');

export function clickTableToolbar(
  table: Table,
  type: 'row' | 'col',
  toolbarDom: HTMLElement,
) {
  return {
    type: TABLE_CLICK_TOOLBAR,
    payload: {
      node: table,
      type,
      toolbarDom,
    },
  };
}

interface TableClickTableToolbarPayload {
  node: Table,
  type: 'row' | 'col',
  toolbarDom: HTMLElement,
}

interface TableClickTableToolbarAction {
  type: typeof TABLE_CLICK_TOOLBAR;
  payload: TableClickTableToolbarPayload;
}

export function isTableClickTableToolbarAction(
  action: Action,
): action is TableClickTableToolbarAction {
  return action.type === TABLE_CLICK_TOOLBAR;
}